import React from 'react'
import {deleteTodo, toggleTodo} from '../todoApi'

class TodoItem extends React.Component {
  toggleTodo() {
    let { todo } = this.props
    toggleTodo(todo.id)
  }
  deleteTodo () {
    let { todo } = this.props
    deleteTodo(todo.id)
  }
  render () {
    let { todo } = this.props
    return (
      <div>
        {todo.done && 
          <span><strike>{todo.text}</strike></span>
        }
        {!todo.done && 
          <span>{todo.text}</span>
        }
        <button onclick={this.toggleTodo.bind(this)}>done</button>
        <button onclick={this.deleteTodo.bind(this)}>delete</button>
      </div>
    )  
  }
}

export default TodoItem
