import Store from './todoStore'

let getTodo = (id) => {
  let todos = Store.get(['todos'])
  for(let i = 0; i < todos.length; i++) {
    if (todos[i].id === id) {
      return {
        index: i,
        todo: todos[i]
      }
    }
  }
  return {}
}

export let deleteTodo = (id) => {
  let todoInfo = getTodo(id).index
  Store.del(['todos', todoInfo.index])
}

