import React from 'react'
import { createTodo } from '../todoApi'

class AddTodo extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      todoText: ''
    }
  }
  createTodo() {
    let { todoText } = this.state
    createTodo()
  }
  handleChange (event) {
    this.setState((state) => {
      state.todoText = event.target.value
      return state
    })
  }
  render () {
    let { todo } = this.props
    return (
      <div>
        <input value={this.state.todoText} onchange={this.handleChange.bind(this)} />
        <button onclick={this.createTodo.bind(this)}>delete</button>
      </div>
    )  
  }
}

export default AddTodo